html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: #1a1b15;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin: 0 auto;
}

.section {
  padding: 2.5rem 0;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  letter-spacing: 0.15rem;
  text-align: center;
  margin: 0;
}

h1 {
  font-size: 3.2rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  font-size: 1.5rem;
}

@media (max-width: 768px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.7rem;
  }

  h3 {
    font-size: 1.8rem;
  }

  p {
    font-size: 1.3rem;
  }
}
@media (max-width: 480px) {
  h1 {
    font-size: 2.8rem;
  }

  h2 {
    font-size: 2.5rem;
  }

  h3 {
    font-size: 1.6rem;
  }

  p {
    font-size: 1.5rem;
  }
}
.button-orange-inline-widthAuto {
  background-color: #FF6D24;
  color: #FFFFFF;
  padding: 1.5rem 4rem;
  width: auto;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border: none;
  transition: background-color 0.3s ease;
  display: inline-block;
}
.button-orange-inline-widthAuto:hover {
  background-color: #f05000;
  cursor: pointer;
}

.button-orange-block-width90 {
  background-color: #FF6D24;
  color: #FFFFFF;
  padding: 1.5rem 4rem;
  width: 90%;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border: none;
  transition: background-color 0.3s ease;
  display: block;
}
.button-orange-block-width90:hover {
  background-color: #f05000;
  cursor: pointer;
}

.button-darkGray-inline-widthAuto {
  background-color: #4E413B;
  color: #FFFFFF;
  padding: 1.5rem 4rem;
  width: auto;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border: none;
  transition: background-color 0.3s ease;
  display: inline-block;
}
.button-darkGray-inline-widthAuto:hover {
  background-color: #312925;
  cursor: pointer;
}

.button-darkGray-block-width90 {
  background-color: #4E413B;
  color: #FFFFFF;
  padding: 1.5rem 4rem;
  width: 90%;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border: none;
  transition: background-color 0.3s ease;
  display: block;
}
.button-darkGray-block-width90:hover {
  background-color: #312925;
  cursor: pointer;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  padding: 3rem 2rem;
  z-index: 99;
  transition: all 0.7s ease;
}
@media (max-width: 930px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 768px) {
  .header {
    flex-direction: row;
  }
  .header .logo {
    font-size: 6rem;
  }
}
@media (max-width: 410px) {
  .header .logo {
    font-size: 4rem;
  }
}
.header.down {
  background-color: #FFFFFF;
  color: #1a1b15;
  padding: 0 2rem;
  -webkit-box-shadow: 0px 0px 39px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 39px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 39px -5px rgba(0, 0, 0, 0.75);
}
.header.down .navbar-link {
  color: #1a1b15;
}
.header.down .navbar-link:hover {
  background-color: rgba(26, 27, 21, 0.2);
  border-bottom: 0.3rem solid #1a1b15;
}
@media (max-width: 768px) {
  .header.down .navbar-link {
    color: #FFFFFF;
  }
  .header.down .navbar-link:hover {
    background-color: rgba(255, 255, 255, 0.2);
    border-bottom: 0.3rem solid #FFFFFF;
  }
}
.header.down .logo {
  color: #1a1b15;
  text-align: center;
}
.header .menu-icon {
  color: #857671;
  font-size: 4rem;
  display: none;
}
@media (max-width: 768px) {
  .header .menu-icon {
    display: inline-block;
  }
}

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60vh;
    background-color: #1a1b15;
    transition: all 0.3s ease;
  }
}
.navbar .navbar-link {
  position: relative;
  width: 50%;
  color: #FFFFFF;
  text-align: center;
  letter-spacing: 0.2rem;
  font-weight: 700;
  transition: all 0.3s ease;
  padding: 2rem 1.5rem;
  border-bottom: 0.3rem solid transparent;
  z-index: 99;
}
.navbar .navbar-link:hover {
  background-color: rgba(255, 255, 255, 0.5);
  border-bottom: 0.3rem solid #FFFFFF;
}

@media (max-width: 768px) {
  .active {
    transform: translate(100%);
  }

  .buttonActive {
    z-index: 99;
    transition: color 0.3s ease;
  }
}
.logo {
  font-size: 6rem;
  font-family: "Dancing Script", cursive;
  font-weight: 300;
  color: #FFFFFF;
  transition: color 0.7s ease;
}
.logo span {
  font-weight: 900;
}

.hero {
  width: 100%;
  height: 100vh;
  background: url("./../img/banner.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  text-align: center;
  color: #FFFFFF;
  position: relative;
}
.hero .hero-content {
  position: relative;
  padding-bottom: 3rem;
}
.hero .hero-content .welcome {
  color: #FF6D24;
  font-size: 3rem;
}
@media (max-width: 768px) {
  .hero .hero-content .welcome {
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  .hero .hero-content .logo {
    font-size: 3rem;
  }
}
@media (max-width: 930px) {
  .hero {
    height: 70vh;
  }
}
@media (max-width: 768px) {
  .hero {
    height: 60vh;
  }
}
@media (max-width: 480px) {
  .hero {
    height: 40vh;
  }
}
@media (max-width: 280px) {
  .hero {
    height: 50vh;
  }
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.about .about-container {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  text-align: justify;
}
@media (max-width: 768px) {
  .about .about-container {
    grid-template-columns: 1fr;
  }
}
.about .about-container .about-info {
  padding: 1rem;
}
.about .about-container .about-info h3 {
  text-align: start;
  margin-top: 3rem;
}
.about .about-container .about-info h2 {
  text-align: start;
  font-weight: 300;
}
.about .about-container .about-info ul {
  padding-left: 1.8rem;
}
.about .about-container .about-info ul li {
  list-style: disc;
}
.about .about-container .about-image img {
  border-radius: 1rem;
}

.typeJobs {
  background-color: #1a1b15;
  color: #bfbfbf;
}
.typeJobs .typeJobs-container p {
  text-align: center;
  max-width: 70%;
  margin: 0 auto;
}
.typeJobs .typeJobs-container .jobs-container {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 768px) {
  .typeJobs .typeJobs-container .jobs-container {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.typeJobs .typeJobs-container .jobs-container .job {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  padding: 2rem;
  -webkit-box-shadow: 0px 0px 10px -6px white;
  -moz-box-shadow: 0px 0px 10px -6px white;
  box-shadow: 0px 0px 10px -6px white;
  border-radius: 1rem;
}
.typeJobs .typeJobs-container .jobs-container .job img {
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
}
.typeJobs .typeJobs-container .jobs-container .job h3 {
  margin: 1rem 0 2rem 0;
  font-weight: 700;
  color: #FFFFFF;
}